@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
.btn{font-weight:bold;min-height:30px;padding:13px 32px;font-size:13px;cursor:pointer;border-radius:4px;letter-spacing:0.26px;line-height:25px}.text-primary{color:#286EFA}.bg-primary{background-color:#286EFA}.align-super{vertical-align:super}.align-sub{vertical-align:sub}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  overflow-x: hidden !important;
}

ul {
  list-style-type: none;
}

